let groupedData: any[] = [];
const Lane: React.FC = () => {
	const [data, setData] = useState<DataType[]>([]); // Â§ÑÁêÜÂêéÁöÑÂÄº
	const [apiData, setApiData] = useState<DataType[]>([]); // Êé•Âè£ËøîÂõûÁöÑÂÄº
	const [loading, setLoading] = useState<Boolean>(true);
	const [error, setError] = useState<String>('');
	async function execFunc() {
		try {
			// const { data } = await axios.get('http://127.0.0.1:6300/shelf/Warehouse_Report')
			setLoading(true);
			const data: any = await GetShelfStock();
			// console.log('data =====', data)
			const rawData = data.data.material;
			setApiData(rawData);
			rawData.forEach((item: any) => {
				const { row__c, lay__c, col__c } = item;
				const key = `${row__c}Êéí - ${lay__c}Â±Ç`;

				let existing = groupedData.find(d => d.key === key);
				if (!existing) {
					existing = {
						key,
						row__c: row__c,
						lay__c: lay__c,
						// column1: null,
						// column2: null,
						...item,
					};
					groupedData.push(existing);
				}
				// ÊåâÂàóÂè∑Â°´ÂÖÖ column1 ~ columnN
				existing[`column${col__c}`] = col__c;
			});
			// console.log('Â§ÑÁêÜÂêéÁöÑrawDataÔºö', rawData) //* ÊÄªÂÖ±24Êù°
			console.log('ÂêàÂπ∂ groupedData', groupedData); //* ÊÄªÂÖ±8Êù°    Â∞ÜÂ∫ì‰ΩçÊï∞ÊçÆÂêàÂπ∂

			// üìå 2Ô∏è‚É£ Â§ÑÁêÜ rowSpanÔºåÂêàÂπ∂Áõ∏ÂêåË°å
			const rowSpanMap = new Map<string, number>();
			groupedData.forEach((item, index) => {
				const key = `${item.row__c}-${item.lay__c}`;
				if (!rowSpanMap.has(key)) rowSpanMap.set(key, groupedData.filter(d => d.row__c === item.row__c && d.lay__c === item.lay__c).length);
			});
			// console.log('Â§ÑÁêÜÊéíÂ∫èÂêé groupedData', groupedData)

			// * ËøôÈáåÊéíÂ∫èÊòØÂõ†‰∏∫ÊåâÁÖßË¥ßÊû∂ÁöÑÊ†∑Â≠ê„ÄÅ‰ªé‰∏ÄÂ±ÇÂà∞ÂõõÂ±Ç
			groupedData.sort((a, b) => {
				if (a.row__c != b.row__c) return a.row__c - b.row__c; // Êåâ row ÂçáÂ∫è
				return b.lay__c - a.lay__c; // Êåâ layer ÈôçÂ∫è
			});
			// console.log('ÊéíÂ∫è groupedData', groupedData)
			setData(groupedData);
			setLoading(false);
		} catch (error) {
			console.log('error Line', error);
			setLoading(false);
		}
	}
	useEffect(() => {
		execFunc();
	}, []);

	if (loading) {
		return <ProSkeleton type='list' />;
	}

	// console.log('Â∑∑ÈÅì‰∏Ä ==================================================================')
	let Header = (
		<div className='flex flex-row justify-between'>
			<div className='flex flex-row'>
				<div className='w-[80px] px-[4px] py-[6px]  text-center text-[12px] bg-slate-100'>Á©∫Â∫ì‰Ωç</div>
				<div className='w-[80px] px-[4px] py-[6px]  text-center text-[12px] bg-pink-300'>È¢ÑÂç†Áî®Â∫ì‰Ωç</div>
				<div className='w-[80px] px-[4px] py-[6px]  text-center text-[12px] bg-green-400'>ÊúâÂ∫ìÂ≠òÂ∫ì‰Ωç</div>
				<div className='w-[80px] px-[4px] py-[6px]  text-center text-[12px] bg-slate-100 text-red-500'>ÈÄâ‰∏≠Â∫ì‰Ωç</div>
			</div>
			<div>
				<Button type='text' icon={<RedoOutlined />} onClick={execFunc}>
					Âà∑Êñ∞
				</Button>
			</div>
		</div>
	);
	// console.log('ÁªìÊûúÔºö ', data)
	return (
		<Table<DataType>
			className='cusTable'
			title={() => Header}
			columns={apiData ? (columns(apiData) as any) : []}
			dataSource={data}
			// scroll={{ x: "max-content" }}
			scroll={{ x: columns(data).length * 150, y: 55 * 11 }}
			pagination={false}
		/>
	);
};

